.input-item {
  margin-top: -5px;
  input {
    border-radius: 0;
    border: none;
    outline: none;
    display: block;
    width: 100%;
    background: none;
    font-family: 'Helvetica';
    font-weight: 400;
    border-bottom: 2px solid var(--border, #1C1C1C);
    height: 41px;
    padding-top: 5px;
    padding-bottom: 17px;
    color: var(--color);

    &:focus {
      border-color: rgba(var(--base), 1);
    }

    &::placeholder {
      color: inherit;
    }
  }

  &.error {
    input {
      border-color: rgba(var(--error), 1);
      color: rgba(var(--error), 1);
    }
  }
}
