.partners {

  &-list {

  }

  &-item {
    width: calc(100% / 6);
    height: 130px;
    background: var(--white);
    border: 1px solid rgba(#A0A0A0, .5);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 15px;
    @media(hover) {
      &:hover {
        img {
          filter: saturate(1);
          opacity: 1;
        }
      }
    }

    @media(min-width: 1150px) {
      &:nth-child(n+7) {
        margin-top: -1px;
      }

      &:not(:nth-child(6n + 1)) {
        margin-left: -1px;
      }
    }

    @media(max-width: 1149px) and (min-width: 720px) {
      width: calc(100% / 3);
      &:nth-child(n+4) {
        margin-top: -1px;
      }

      &:not(:nth-child(3n + 1)) {
        margin-left: -1px;
      }
    }

    @media(max-width: 719px) {
      width: calc(100% / 2);
      padding: 10px;
      &:nth-child(n+3) {
        margin-top: -1px;
      }

      &:not(:nth-child(2n + 1)) {
        margin-left: -1px;
      }
    }

    img {
      opacity: .5;
      display: flex;
      width: auto;
      max-width: 100%;
      max-height: 100%;
      transition: .3s;
      filter: saturate(0);
    }
  }
}
