.whyus {
  &-list {
    @media(max-width: 1149px) {
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
    @media(max-width: 719px) {
      grid-template-columns: 100%;
    }
  }

  &-item {
    border-top: 8px solid var(--text);
    padding-top: 22px;

    &__title {
      @media(max-width: 719px) {
        br {
          display: none;
        }
      }
    }

    &__text {
      margin-top: 10px;
    }
  }
}
