.networks {
  padding-bottom: 0;

  &-header {
    margin-bottom: 20px;

    .page-title {
      margin-bottom: 0;
    }
  }

  &-item {
    border-top: 8px solid var(--text);

    &:nth-child(even) {
      @media(min-width: 720px) {
        .page-columns:before {
          content: '';
        }
      }
      .networks-item-image {
        @media(min-width: 720px) {
          order: -1;
          right: auto;
          left: 0;
        }
      }
    }

    .page-columns {
      position: relative;
      padding-top: 40px;
      @media(max-width: 1149px) {
        padding-top: 20px;
      }
    }

    &-image {
      text-align: center;

      @media(min-width: 720px) {
        position: absolute;
        top: 40px;
        right: 0;
      }
      @media(max-width: 719px) {
        margin-bottom: 35px;
      }

      img {
        width: auto;
        max-width: 100%;
      }
    }

    &-info {
      h2 {
        margin-bottom: 30px;
        @media(max-width: 719px) {
          text-align: center;
          margin-bottom: 10px;
        }
      }

      p {
        font-size: 24px;
        line-height: 133%;
        margin-bottom: 1.33em;
        text-align: justify;

        &:last-child {
          margin-bottom: 0;
        }

        @media(max-width: 1149px) {
          font-size: 16px;
        }
      }
    }
  }

  &-social {
    margin-top: 105px;
    border-bottom: 2px solid var(--text);
    padding-bottom: 70px;

    @media(max-width: 1149px) {
      margin-top: 50px;
      padding-bottom: 50px;
    }
  }

  &-buttons {
    border-top: 2px solid var(--text);
    padding: 40px 0 30px;
    @media(max-width: 1149px) {
      padding: 20px 0;
    }
  }

  &-button {
    font-family: 'Helvetica';
    font-weight: 300;
    font-size: 56px;
    line-height: 114%;
    opacity: 0.25;
    text-transform: uppercase;
    cursor: pointer;

    img {
      display: none;
    }

    @media(max-width: 1149px) {
      font-size: 36px;
    }
    @media(max-width: 719px) {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      span {
        display: none;
      }
      img {
        display: block;
        height: 100%;
      }
    }


    &.active {
      opacity: 1;
    }
  }
}
