.process {
  padding-bottom: 30px;
  @media(max-width: 1149px) {
    padding-bottom: 15px;
  }
  .page-title {
    p {
      margin-top: 25px;
      max-width: 1020px;
    }
  }

  &-list {
    counter-reset: num;
    @media(min-width: 1150px) {
      margin-bottom: -35px;
      &:before, &:after {
        content: "";
        display: table;
      }
      &:after {
        clear: both;
      }
    }
  }

  &-item {
    counter-increment: num;
    display: flex;
    gap: 25px;
    position: relative;
    width: 440px;

    margin-bottom: 35px;
    min-height: 160px;
    @media(max-width: 1149px) {
      min-height: auto;
      width: 100%;
      gap: 20px;
    }


    @media(min-width: 1150px) {
      float: left;
      clear: left;
      &:nth-child(4n + 2),
      &:nth-child(4n + 3) {
        float: right;
        max-width: 429px;
        clear: right;
      }
      &:nth-child(4n + 1) {
        .process-item__line {
          margin-left: 10px;
        }
      }
      &:nth-child(4n + 2),
      &:nth-child(4n + 4) {
        .process-item__line {
          left: 15px;
          top: 47px;
          width: 3px;
          height: auto;
          bottom: -13px;
          background-image: linear-gradient(to top, #fff 33%, #555555 33%, #555555 66%, #fff 66%);
          background-size: 3px 6px;

          &:before {
            transform: translate(-50%, -50%) rotate(90deg);
          }
        }
      }

      &:nth-child(4n + 3) {
        .process-item__line {
          right: 100%;
          left: auto;
          margin-right: 22px;
          transform: rotate(180deg);
        }
      }

      &:last-child {
        min-height: auto;
      }
    }


    &:before {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      border-radius: 50%;
      border: 2px solid #FF0000;
      display: flex;
      align-items: center;
      justify-content: center;
      content: counter(num);
      font-size: 24px;
      line-height: 24px;
      color: #FF0000;
      @media(max-width: 1149px) {
        font-size: 20px;
      }
    }

    &__text {
      font-size: 24px;
      line-height: 133%;
      @media(max-width: 1149px) {
        font-size: 20px;
        br {
          display: none;
        }
      }
    }

    &__line {
      width: 214px;
      position: absolute;
      left: 100%;
      top: 15px;
      height: 3px;
      background-image: linear-gradient(to left, #fff 33%, #555555 33%, #555555 66%, #fff 66%);
      background-size: 6px 3px;

      &:before {
        width: 34px;
        height: 14px;
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        background-image: url(/img/arr.svg);
        background-size: 8px 14px;
        background-position: center;
        background-repeat: no-repeat;
        background-color: #fff;
      }

      @media(max-width: 1149px) {
        display: none;
      }
    }
  }
}
