.maintenance {
  position: relative;
  overflow: hidden;
  @media(max-width: 1149px) {
    padding-bottom: 285px;
  }

  &-img {
    position: absolute;
    bottom: -30px;
    right: 50%;
    width: auto;
    transform: translateX(-215px);
    @media(max-width: 1149px) {
      transform: translateX(40%);
      width: 503px;
      bottom: -130px;
    }
  }

  &-list {
    max-width: 716px;
    margin-left: auto;
    @media(max-width: 1149px) {
      max-width: none;
    }
  }

  &-item {
    border-top: 8px solid #fff;
    padding-top: 20px;
    margin-bottom: 55px;

    h3 {
      font-family: 'Roboto';
    }

    p {
      margin-top: 10px;
    }
  }

  &-footer {
    border-top: 2px solid #fff;
    padding-top: 30px;

    p {
      font-size: 24px;
      line-height: 133%;
      @media(max-width: 1149px) {
        font-size: 20px;
        line-height: 150%;
      }
    }
  }
}
