.main {
  padding-bottom: 20px;

  &-container {
    gap: 70px;
    @media(max-width: 959px) {
      flex-direction: column;
      gap: 30px;
    }
  }

  &-wrapper {
    flex-shrink: 0;
    width: 740px;
    @media(max-width: 1149px) {
      width: 470px;
      flex-grow: 1;
    }
    @media(max-width: 959px) {
      width: 100%;
    }

    p {
      margin-top: 10px;
    }

    .btn {
      margin-top: 35px;
      width: 100%;
      max-width: 316px;
      @media(max-width: 959px) {
        margin-top: 20px;
      }
    }
  }

  &-img {
    img {
      width: auto;
      max-width: 100%;
      display: block;
    }
  }
}

.index-yandex {
  .main {
    padding-bottom: 0;
    @media(min-width: 960px) {
      &-container {
        gap: 20px;
      }
      &-wrapper {
        max-width: 685px;
        padding-top: 10px;
      }
      &-img {
        align-self: flex-end;
        align-items: flex-end;

        img {
          width: auto;
          max-width: none;
          margin-right: -160px;
          position: relative;

          &:first-child {
            z-index: 1;
          }
        }
      }
    }
    @media(max-width: 959px) {
      &-img {

        img {
          max-width: none;
          margin-right: -80px;
          position: relative;
          width: 455px;

          &:first-child {
            z-index: 1;
            width: 145px;
          }
        }
      }
    }
  }
}
