.tariff {
  &-list {
    gap: 40px;
  }

  &-item {
    gap: 60px;
    @media(max-width: 1149px) {
      gap: 30px;
    }
    @media(max-width: 719px) {
      flex-direction: column;
    }

    &__left {
      font-family: 'Helvetica';
      font-weight: 700;
      line-height: 1;
      flex-shrink: 0;
      background-color: var(--light);
      padding: 20px 30px 35px;
      align-self: flex-start;
      color: var(--white);
    }

    &__price {
      margin-top: 20px;
      gap: 15px;
    }

    &__icon {
      margin-bottom: 15px;

      svg {
        display: block;
        width: 49px;
        height: 56px;
        stroke: var(--white);
      }
    }

    &__old {
      font-size: 32px;
      position: relative;
      display: inline-block;
      @media(max-width: 1149px) {
        font-size: 24px;
      }

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: -6px;
        right: -6px;
        background: var(--white);
        opacity: .6;
        height: 2px;
      }
    }

    &__value {
      font-size: 56px;
      @media(max-width: 1149px) {
        font-size: 36px;
      }
    }

    &__plan {
      margin-top: 20px;
      padding-top: 20px;
      border-top: 2px solid rgba(255, 255, 255, 0.6);
      &-title {
        align-items: flex-end;
        gap: 10px;
        margin-bottom: 20px;
        svg {
          width: 22px;
          height: 22px;
          fill: var(--white);
        }
      }
      ul {
        color: #fff;
        list-style: none;
      }
      li {
        position: relative;
        padding-left: 32px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        &:before {
          content: url(/img/ul-white.svg);
          position: absolute;
          top: 1px;
          left: 0;
        }
      }
    }

    &__right {
      flex-grow: 1;
      padding-top: 30px;
      border-top: 8px solid var(--light);
      display: flex;
      flex-direction: column;
      @media(max-width: 719px) {
        width: 100%;
      }

      p {
        margin-bottom: 1em;
        font-size: 24px;
        line-height: 133%;
        color: var(--text);
        @media(max-width: 1149px) {
          font-size: 20px;
        }
      }

      .page-btn {
        margin-top: auto;
      }
    }
  }
}
