.suitable {
  background: #F6F6F6;
  @media(max-width: 1149px) {
    padding: 60px 0 55px;
  }
  &-list {
    border-top: 8px solid var(--text);
    position: relative;
  }

  &-img {
    position: absolute;
    top: 50%;
    left: 0;
    width: auto;
    transform: translateY(-50%);
    @media(max-width: 1149px) {
      width: 100%;
      max-width: 290px;
      display: block;
      margin: 35px auto 0;
      position: relative;
      top: 0;
      transform: none;
    }
  }

  &-item {
    border-top: 2px solid var(--text);
    padding-top: 20px;
    margin-top: 30px;
    max-width: 716px;
    margin-left: auto;

    @media(max-width: 1149px) {
      max-width: none;
    }

    &:first-child {
      border: none;
      padding-top: 0;
      @media(max-width: 1149px) {
        margin-top: 20px;
      }
    }

    p {
      font-size: 24px;
      line-height: 133%;
      margin-top: 10px;
      @media(max-width: 1149px) {
        font-size: 16px;
      }
    }
  }
}
