.btn {
  position: relative;
  height: 56px;
  background: none;
  outline: none;
  border: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  font-weight: 400;
  font-family: 'Helvetica';
  font-size: 16px;
  line-height: 125%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border-radius: var(--radiusItem);
  cursor: pointer;
  white-space: nowrap;
  transition: .3s color, .3s border, .3s filter;


  &-white {
    color: var(--text);
    background: var(--white);
    @media(max-width: 1149px) {
      color: var(--light, var(--text));
    }
    @media(hover) {
      &:hover {
        color: var(--light);
      }
    }
  }

  &-base {
    color: var(--white);
    background: var(--light);
    @media(hover) {
      &:hover {
        color: var(--white);
        filter: hue-rotate(20deg);
      }
    }
  }
}
