*, *:before, *:after {
  box-sizing: border-box;
}

img, .btn {
  user-select: none;
}

p, ul, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

[data-modal] {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: unset;
  @media(hover) {
    transition: .3s;
    &:hover {
      color: rgba(var(--base), 1);
      //svg {
      //  fill: rgba(var(--base), 1);
      //  stroke: rgba(var(--base), 1);
      //}
    }
  }
}


body {
  position: relative;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
  z-index: 1;
  font-size: 16px;
  line-height: 125%;
  color: var(--text);
  @media(max-width: 1159px) {
    padding-top: 65px;
  }
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;

  main {
    flex-grow: 1;
  }

  .footer {
    margin-top: 0;
  }
}

main {
  > * {
    padding: 80px 0 115px;
    @media(max-width: 1149px) {
      padding: 40px 0 55px;
    }
  }
}

.container {
  max-width: 1150px;
  padding: 0 15px;
  margin: 0 auto;
}

.f {
  display: flex;

  &-ac {
    align-items: center;
  }

  &-ae {
    align-items: flex-end;
  }

  &-w {
    flex-wrap: wrap;
  }

  &-js {
    justify-content: space-between;
  }

  &-jc {
    justify-content: center;
  }

  &-dc {
    flex-direction: column;
  }
}

.page-form {
  position: relative;

  &.active {
    .page-form {
      &-send {
        display: none;
      }

      &-ok {
        display: block;
      }
    }
  }

  &-ok {
    display: none;
    width: 100%;
    height: 100%;
  }
}

h1, h2 {
  font-family: 'Helvetica';
  font-weight: 700;
  font-size: 56px;
  line-height: 114%;
  @media(max-width: 1149px) {
    font-size: 36px;
    line-height: 111%;
  }
}

h3 {
  font-family: 'Helvetica';
  font-weight: 700;
  font-size: 32px;
  line-height: 125%;
}

h4 {

}

p, ul, ol {
  line-height: 150%;
  color: var(--p);
}

.page-title {
  margin-bottom: 55px;
  @media(max-width: 1149px) {
    margin-bottom: 25px;
  }

  p {
    margin-top: 15px;
    font-size: 24px;
    line-height: 133% ;
    @media(max-width: 1149px) {
      font-size: 20px;
      line-height: 150%;
    }
  }
}

.page-bg {
  --border: #fff;
  --color: #fff;
  background-color: var(--base);
  color: var(--white);

  p {
    color: var(--white);
  }
}

.page-btn {
  margin-top: 30px;

  .btn {
    width: 100%;
    max-width: 312px;
  }
}

.page-columns {
  display: grid;
  grid-template-columns: 344px 312px 344px;
  grid-gap: 45px 60px;

  &_end {
    align-items: flex-end;
  }

  &_center {
    align-items: center;
  }

  &_two {
    grid-template-columns: 1fr 1fr;
  }

  @media(max-width: 1149px) {
    grid-template-columns: 100%;
    grid-gap: 30px;
  }
  @media(min-width: 720px) {
    &_area {
      @media(max-width: 1149px) {
        grid-template-columns: 315px 1fr 315px;
      }
    }
    &_span {
      grid-column: span 2;
    }
  }
}

.tabs-item {
  &:not(.active) {
    display: none;
  }
}
