.questions {
  &-list {
    border-top: 8px solid var(--text);
    gap: 20px;
    padding-top: 20px;
  }

  &-item {
    border-bottom: 2px solid var(--text);
    padding-bottom: 30px;

    &.active {
      .questions-item-text {
        max-height: calc(var(--height) + 10px);
      }
      .questions-item-icon:after {
        transform: translate(-50%, -50%) rotate(0deg);
      }
    }

    &-header {
      gap: 35px;
      cursor: pointer;
    }

    &-icon {
      display: block;
      position: relative;
      width: 24px;
      height: 40px;
      flex-shrink: 0;
      &:before,
      &:after {
        content: '';
        position: absolute;
        width: 24px;
        height: 4px;
        background: #FF0000;
        transition: .3s;
        left: 50%;
        top: 50%;
      }
      &:before {
        transform: translate(-50%, -50%);
      }
      &:after {
        transform: translate(-50%, -50%) rotate(90deg);
      }
    }

    &-text {
      overflow: hidden;
      max-height: 0;
      transition: .3s;
      p {
        font-size: 24px;
        line-height: 133%;
        margin-bottom: 0.5em;
        @media(max-width: 1149px) {
          font-size: 20px;
        }
        @media(max-height: 719px) {
          br {
            display: none;
          }
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &-body {
      padding-top: 20px;
    }
  }
}


