.creating {
  background-image: linear-gradient(0deg, #52BDF2, #52BDF2);
  position: relative;
  @media(max-width: 1149px) {
    padding-bottom: 365px;
  }

  .page-title {
    p {
      max-width: 988px;
    }
    h2 {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &-logo {
    @media(max-width: 1149px) {
      position: absolute;
      top: 10px;
      right: 15px;
    }
    img {
      display: block;
      width: auto;
      @media(max-width: 1149px) {
        width: 41px;
      }
    }
  }

  &-list {
    counter-reset: num;
    gap: 50px;
    @media(max-width: 1149px) {
      gap: 20px;
    }
  }

  &-item {
    border-top: 8px solid var(--white);
    counter-increment: num;
    padding-top: 20px;
    @media(max-width: 1149px) {
      display: block;

      &.active {
        .creating-item-text {
          max-height: calc(var(--height) + 10px);
        }
        .creating-item-icon:after {
          transform: translate(-50%, -50%) rotate(0deg);
        }
      }
    }

    &:before {
      content: counter(num);
      font-family: 'Helvetica';
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      @media(max-width: 1149px) {
        display: none;
      }
    }

    p {
      margin-top: 10px;
    }

    &-header {
      display: flex;
      justify-content: space-between;
      gap: 10px;
      @media(max-width: 1149px) {
        cursor: pointer;
      }
    }

    &-icon {
      display: none;
      @media(max-width: 1149px) {
        display: block;
        position: relative;
        width: 24px;
        height: 40px;
        flex-shrink: 0;
        &:before,
        &:after {
          content: '';
          position: absolute;
          width: 24px;
          height: 4px;
          background: #fff;
          transition: .3s;
          left: 50%;
          top: 50%;
        }
        &:before {
          transform: translate(-50%, -50%);
        }
        &:after {
          transform: translate(-50%, -50%) rotate(90deg);
        }
      }
    }

    &-text {
      @media(max-width: 1149px) {
        transition: .3s;
        overflow: hidden;
        max-height: 0;
      }
    }
  }

  &-footer {
    border-top: 2px solid var(--white);
    margin-top: 45px;
    padding-top: 30px;
    @media(max-width: 1149px) {
      display: block;
      margin-top: 20px;
    }

    &:before {
      content: '';
      @media(max-width: 1149px) {
        display: none;
      }
    }

    p {
      font-size: 24px;
      line-height: 133%;
      @media(max-width: 1149px) {
        font-size: 20px;
      }
    }
  }

  &-img {
    position: absolute;
    bottom: 0;
    right: 50%;
    transform: translateX(-160px);
    display: block;
    width: auto;
    @media(max-width: 1149px) {
      transform: translateX(50%);
    }
  }
}
